<template>
    <div>
      <el-button type="primary" @click="$router.push('/')">Back</el-button>
      <ul>
        <li v-for="(item, index) in logs" :key="index" style="padding: 10px">
          <el-tag>{{getDate(item.date.seconds)}}</el-tag>
          <template v-if="item.count > 0"> add <el-tag>{{item.count}}</el-tag> jobs </template>
          <template v-else> no new vacations </template>
          from <el-tag>{{item.source}}</el-tag>
        </li>
      </ul>
    </div>
</template>

<script>
import { db } from '../main'

export default {
  name: 'Logs',

  data () {
    return {
      logs: []
    }
  },

  firestore () {
    return {
      logs: db.collection('logs').orderBy('date', 'desc')
    }
  },

  methods: {
    getDate (date) {
      return new Date(date * 1000).toLocaleDateString('ru-ru', { hour: 'numeric', minute: 'numeric' })
    }
  }
}
</script>

